.CommandListItem-module__commandListItem___uArdC {
    cursor: pointer;
    display: flex !important; /* Necessary due to the neos header bar styling */
    gap: 1em;
    padding: 0.4rem 0.6rem;
    align-items: baseline;
    border-radius: var(--border-radius-item);
    transition: background-color 0.1s ease-in-out;
    background-color: transparent;
    user-select: none;
}

.CommandListItem-module__commandListItem___uArdC:hover {
    background-color: var(--color-item-hover);
}

.CommandListItem-module__highlighted___2xHNL {
    background-color: var(--color-item-highlighted);
}
