:root {
    --bar-min-width: 400px;
    --bar-max-width: 600px;
    --bar-width: 40vw;
    --bar-background: rgba(0, 0, 0, 0.8);
    --footer-background: rgba(0, 0, 0, 0.8);
    --color-shadow: rgba(255, 255, 255, 0.7);
    --color-light-text: rgba(255, 255, 255, 0.6);
    --color-icon-bg: rgba(255, 255, 255, 0.2);
    --color-icon-bg-hover: rgba(255, 255, 255, 0.4);
    --color-border: rgba(255, 255, 255, 0.2);
    --color-item-highlighted: rgba(255, 255, 255, 0.15);
    --color-item-hover: rgba(255, 255, 255, 0.1);
    --border-radius-bar: 0.6rem;
    --border-radius-item: 0.4rem;
}

.CommandBar-module__commandBar___iIl8n {
    backdrop-filter: blur(10px);
    background: var(--bar-background);
    border-radius: var(--border-radius-bar, 5);
    border: 1px solid var(--color-border);
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.5);
    color: white;
    display: flex;
    flex-direction: column;
    left: 50%;
    margin: 0;
    max-width: var(--bar-max-width);
    min-width: var(--bar-min-width);
    overflow: hidden;
    position: absolute;
    padding: 0;
    top: 50%;
    translate: -50% -50%;
    width: var(--bar-width);
    pointer-events: all;
}

.CommandBar-module__commandBar___iIl8n small {
    color: var(--color-light-text);
}

.CommandBar-module__resultsWrap___2ZfKL {
    display: grid;
    grid-template-rows: 0fr;
    transform-origin: top center;
    transition: grid-template-rows 0.2s ease-in;
}

.CommandBar-module__resultsWrap___2ZfKL.CommandBar-module__expanded___2L-st {
    grid-template-rows: 1fr;
}

.CommandBar-module__resultsWrap___2ZfKL.CommandBar-module__expanded___2L-st > * {
    opacity: 1;
    padding: 0.5rem;
}
